<template>
  <div id="home" ref="home">
  </div>
</template>

<script>
import axios from "axios";
import marked from "marked";

export default {
  name:"Home",
  mounted:function(){
    axios.get("https://api.github.com/repos/tdoca/blog/issues?page=1").then((res)=>{
      this.$refs.home.innerHTML=marked(res.data[0].body);
      console.log(new Date(res.data[0].created_at));
    }).catch((error)=>{
      console.log(error);
    })
  }
}
</script>

<style>
#home{
  min-height: 500px;
}
</style>