<template>
    <div></div>
</template>
<script>
import axios from "axios"
import marked from "marked"

export default {
    name: "page",
    mounted:function(){
    axios.get("https://api.github.com/repos/tdoca/tdoca/issues?page=1").then((res)=>{
      console.log(res.data);
      this.$refs.home.innerHTML=marked(res.data[0].body);
    }).catch((error)=>{
      console.log(error);
    })
  }
}
</script>