import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home.vue'
import page from "../views/page.vue"

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../views/about.vue')
  },
  {
    path: "/page",
    name: "page",
    component: page
  }
]

const router = new VueRouter({
  routes
})

export default router
